*{
  margin: 0;
  padding: 0;
}
.main{
  width: 100%;
  height: 100dvh;
}
.top-1{
  border: 2px solid black;
  border-radius: 20px;
  margin: 10px;
  padding: 0.5rem;
  color: #fff;
  background-image: linear-gradient(45deg,rgb(46, 45, 45), rgb(79, 77, 77));
}
.top-2{
  border: 2px solid black;
  border-radius: 20px;
  margin: 10px;
  padding: 0.5rem;
  color: #fff;
  background-image: linear-gradient(45deg,rgb(46, 45, 45), rgb(79, 77, 77));
}
.scrolling-wrapper {
  overflow-x: scroll;
  overflow-y: hidden;
  white-space: nowrap;
  height: 30rem;
  padding: 1rem;

  .camera {
    display: inline-block;
    height: 25rem;
  width: 40%;
  border: 2px solid black;
  margin: 10px;
  color: #fff;
  background-image: linear-gradient(45deg,rgb(46, 45, 45), rgb(79, 77, 77));
  border-radius: 20px;
  }
}
.hr{
  border: 0.2px solid rgb(122, 117, 117);
}
.dot {
  height: 15px;
  width: 15px;
  background-color: #fff;
  border-radius: 50%;
  display: inline-block;
}
